* {
  margin: 0;
}

body {
  margin: 0;
  padding : 0;
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #DFE3E8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.conSettingsMain {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width:90%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5%;
    grid-gap: 28px;
    gap: 28px;
    height: 80vh;
}

.conSettingsMain .conSettingsCategory{
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
    align-self: stretch;
}

.conSettingsMain h1{
    background: linear-gradient(270deg, #385EC0 40.19%, #3FAC8C 109.29%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
}

.conSettingsMain .conSettingsCategory p {
    background: linear-gradient(270deg, #1D3874 40.19%, #3FAC8C 109.29%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
  }

.conSettingsMain .conSettingsCategory select{
    background-color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 50rem;
    color: #1D3874;
    display: flex;
    font-size: 16px;
}

.conSettingsMain .conSettingsCategory input {
    background-color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 50rem;
    color: #1D3874;
    display: flex;
    text-decoration: none;
    font-size: 16px;
}

.conSettingsMain .conSettingsCategory input:focus{
    outline: none;
    cursor: none;
}
.questionMainCon {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  width:90%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5%;
  grid-gap: 32px;
  gap: 32px;
  height:80vh;
}

.headerQuestion {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
}

.headerQuestion span {
  position: absolute;
  left:90%;
  font-size: 20px;
  color: #1D3874;
  cursor: pointer;
}

.progressBarBG {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  grid-gap: 10px;
  gap: 10px;
  width: 100px;
  height: 10px;
  background: #f6f8fb;
  border-radius: 99px;
}

.progressBarValue {
  height: 10px;
  background: linear-gradient(90deg, #3fac8c 0%, #385ec0 100%);
  border-radius: 99px;
  flex: none;
  order: 0;
  flex-grow: 0;
  transition: width 0.8s ease-out;
}

.conQuestion {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 28px 4%;
  grid-gap: 20px;
  gap: 20px;
  background: #ffffff;
  border-radius: 20px;
  align-self: stretch;
  min-height: 280px;
}
.conQuestion ul {
  flex: none;
  order: 1;
  align-self: stretch;
  flex-grow: 0;
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}
.conQuestion ul li {
  color: #1d3874;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 36px;
  grid-gap: 16px;
  gap: 16px;
  background: #dfe3e8;
  border-radius: 999px;
  pointer-events: auto;
  cursor: pointer;
  font-size: 16px;
}

.conQuestion ul .selected {
  color: white;
  background: linear-gradient(90deg, #3fac8c 0%, #385ec0 100%);
}

.conQuestion ul .right {
  color: white;
  background: linear-gradient(90deg, #88B628 0%, #259A46 100%);
  transition: background 5s;
}

.conQuestion ul .wrong {
  color: white;
  background: linear-gradient(90deg, #EF6D5B 0%, #A5166B 100%);
}

.conScore {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 28px 20px;
  grid-gap: 20px;
  gap: 20px;
  background: #ffffff;
  border-radius: 20px;
  align-self: stretch;
  min-height: 200px;
}
.score {
  font-size: 130px;
  font-weight: 500;
  background: linear-gradient(90deg, #3fac8c 0%, #385ec0 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

p {
  color: #1D3874;
  font-size: 16px;
}

.IconRigth {
  display: none;
  position:absolute;
  left:11%;
}

.IconWrong {
  display: none;
  position:absolute;
  left:11%;
}

.App {
  margin: 0px;
  padding:0px;
  text-align: left;
  height: 100%;
}

.btn {
  border-radius: 100rem;
  padding: 12px 36px;
  color: #1D3874;
  border: solid 3px transparent;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(90deg, #3fac8c 0%, #385ec0 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #fff inset;
  cursor: pointer;
  align-self: stretch;
  margin: 0px 20px;
  font-size: 16px;
}



